.footer{
    display: flex;
    align-items: center;
    margin: -.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;

    .f-content{
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;
        gap: 4rem;
        color: #fff;
        margin-top: 4rem;
        .f-icons{
            display: flex;
            gap: 2rem;
        }
    }
}